<template lang="pug">
    .error__content
        .error_message {{message}}
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
    data() {
        return {
        }
    },
    mounted() {
        this.setErrorTime()
    },
    props: {
        message: String
    },
    computed: {
        ...mapState ("error", {
            error: state => state.error
        })
    },
    methods: {
        ...mapActions("error", ["removeError"]),
        setErrorTime(){
            setTimeout(() => this.removeError(), 3000);
        }
    }
}
</script>
<style lang="scss">
.error__content {
    width: 25%;
    padding: 2%;
    line-height: 1.5rem;
    position: absolute;
    font-family: "Alegreya Sans Extra Bold";
    right: 0;
    bottom: -8px;
    background: #e6000a;
    color: #464549;
    border-radius: 5px;
    text-align: center;
    opacity: 0.9;
    &:after{
        position: absolute;
        content: '';
        width: 30px;
        height: 30px;
        bottom: -18%;
        border-radius: 4px;
        transform: rotate(45deg);
        background: #e6000a;
    }
}
</style>